<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <h3></h3>
    <div class="order-info">
      <p>
        {{orders.business.businessName || '...'}}
        <i class="fa fa-caret-down" @click="detailetShow"></i>
      </p>
      <p>&#165;{{orders.orderTotal.toFixed(2)}}</p>
    </div>
    <!--  -->
    <ul class="order-detailet" v-show="isShowDetailet">
      <li v-for="item in orders.list" :key="item.foodId">
        <p>{{item.food?.foodName || ''}} x {{item.quantity}}</p>
        <p>&#165;{{((item.food?.foodPrice || 0) * item.quantity).toFixed(2)}}</p>
      </li>
      <li>
        <p></p>
        <p>&#165;{{orders.business.deliveryPrice.toFixed(2)}}</p>
      </li>
    </ul>
    <!--  -->
    <ul class="payment-type">
      <li @click="selectPayment(1)">
        <img src="../assets/alipay.png">
        <i class="fa fa-check-circle" v-show="selectedPayment === 1"></i>
      </li>
      <li @click="selectPayment(2)">
        <img src="../assets/wechat.png">
        <i class="fa fa-check-circle" v-show="selectedPayment === 2"></i>
      </li>
    </ul>
    <div class="payment-button">
      <button @click="confirmPayment"></button>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>

<script>
import Footer from '@/views/Footer.vue';
import { orderApi } from '@/api/order';
import { businessApi } from '@/api/business';

export default {
  name: 'Payment',
  data() {
    return {
      orderId: this.$route.query.orderId,
      orders: {
        business: {
          businessName: '',
          deliveryPrice: 0
        },
        list: [],
        orderTotal: 0
      },
      isShowDetailet: false,
      selectedPayment: 1
    };
  },
  created() {
    if (!this.orderId) {
      this.$message.error('ID');
      this.$router.push('/index');
      return;
    }
    console.log('ID:', this.orderId);
    this.loadOrderData();
  },
  mounted() {
    history.pushState(null, null, document.URL);
    window.onpopstate = () => {
      this.$router.push({ path: '/index' });
    };
  },
  destroyed() {
    window.onpopstate = null;
  },
  methods: {
    async loadOrderData() {
      try {
        // 
        const orderData = await orderApi.getOrdersById(this.orderId);
        console.log(':', orderData);

        if (!orderData) {
          this.$message.error('');
          return;
        }

        // 
        try {
          const businessResponse = await businessApi.getBusinessById(orderData.businessId);
          console.log(':', businessResponse);

          if (businessResponse) {
            orderData.business = businessResponse;
          }
        } catch (error) {
          console.error(':', error);
        }

        // 
        this.orders = {
          ...orderData,
          business: orderData.business || {
            businessName: '',
            deliveryPrice: 0
          },
          list: orderData.list || [],
          orderTotal: orderData.orderTotal || 0
        };

        console.log(':', this.orders);
      } catch (error) {
        console.error(':', error);
        if (error === 'No token' || (error.response && error.response.status === 401)) {
          localStorage.removeItem('token');
          this.$removeSessionStorage('user');
          this.$router.push('/login');
        } else {
          this.$message.error('');
        }
      }
    },
    detailetShow() {
      this.isShowDetailet = !this.isShowDetailet;
    },
    selectPayment(type) {
      this.selectedPayment = type;
    },
    async confirmPayment() {
      try {
        console.log('');

        // 
        const confirm = window.confirm('');
        console.log(':', confirm);
        if (!confirm) return;

        console.log('');

        // 
        const params = {
          orderId: this.orderId
        };

        const response = await orderApi.updateOrderState(params);
        console.log(':', response);

        //  Integer 
        let success = false;
        if (response && typeof response.data === 'number' && response.data > 0) {
          success = true;
        } else if (response && typeof response === 'number' && response > 0) {
          // 
          success = true;
        }

        if (success) {
          console.log('');
          this.$message.success('');
          this.$router.push({
            path: '/OrderList',
            query: {
              orderId: this.orderId
            }
          });
        } else {
          console.log('');
          this.$message.error('');
        }
      } catch (error) {
        console.error(':', error);
        if (error.response) {
          //  HTTP 
          if (error.response.status === 401) {
            localStorage.removeItem('token');
            this.$removeSessionStorage('user');
            this.$router.push('/login');
          } else {
            console.error(':', error.response.status);
            this.$message.error(`: ${error.response.status}`);
          }
        } else if (error.request) {
          // 
          console.error(':', error.request);
          this.$message.error('');
        } else {
          // 
          console.error(':', error);
          this.$message.error('');
        }
      }
    }
  },
  components: {
    Footer
  }
};
</script>

<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}
/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
/******************  ******************/
.wrapper h3 {
  margin-top: 12vw;
  box-sizing: border-box;
  padding: 4vw 4vw 0;
  font-size: 4vw;
  font-weight: 300;
  color: #999;
}
.wrapper .order-info {
  box-sizing: border-box;
  padding: 4vw;
  font-size: 4vw;
  color: #666;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  margin-bottom: 2vw;
}
.wrapper .order-info p:first-child {
  display: flex;
  align-items: center;
  gap: 2vw;
}
.wrapper .order-info p:last-child {
  color: #f60;
  font-weight: 500;
}
/******************  ******************/
.wrapper .order-detailet {
  background: #fff;
  margin-bottom: 4vw;
}
.wrapper .order-detailet li {
  padding: 3vw 4vw;
  border-bottom: 1px solid #f5f5f5;
}
.wrapper .order-detailet li:last-child {
  border-bottom: none;
  background: #f8f8f8;
}
.wrapper .order-detailet li p {
  font-size: 3vw;
  color: #666;
}
/******************  ******************/
.wrapper .payment-type {
  width: 100%;
}
.wrapper .payment-type li {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrapper .payment-type li img {
  width: 33vw;
  height: 8.9vw;
}
.wrapper .payment-type li .fa-check-circle {
  font-size: 5vw;
  color: #38CA73;
}
.wrapper .payment-button {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw;
}
.wrapper .payment-button button {
  width: 100%;
  height: 10vw;
  border: none;
  /**/
  outline: none;
  border-radius: 4px;
  background-color: #38CA73;
  color: #fff;
  font-size: 4.5vw;
}
</style>
