<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <ul class="form-box">
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="text" @blur="checkUserId" v-model="user.userId" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="password" v-model="user.password" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="password" v-model="confirmPassword" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="text" v-model="user.userName" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content" style="font-size: 3vw;">
          <input type="radio" v-model="user.userSex" value="1"
                 style="width:6vw;height: 3.2vw;">
          <input type="radio" v-model="user.userSex" value="0"
                 style="width:6vw;height: 3.2vw;">
        </div>
      </li>
    </ul>
    <div class="button-login">
      <button @click="register"></button>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from '@/views/Footer.vue';
import { userApi } from '@/api/user';

export default {
  name: 'Register',
  data() {
    return {
      user:{
        userId:'',
        password:'',
        userName:'',
        userSex:1
      },
      confirmPassword:''
    }
  },
  methods: {
    async checkUserId(){
      try {
        const response = await userApi.checkUserId(this.user.userId);
        if(response === 1){
          this.user.userId = '';
          alert('')
        }
      } catch(error) {
        console.error('ID:', error);
      }
    },

    async register() {
      if (this.user.userId === '') {
        alert('');
        return;
      }
      if (this.user.password === '') {
        alert('');
        return;
      }
      if (this.user.password !== this.confirmPassword) {
        alert('');
        return;
      }
      if (this.user.userName === '') {
        alert('');
        return;
      }

      try {
        // 
        const response = await userApi.register(this.user);
        console.log(':', response); // 
        if(response > 0){
          alert('');
          this.$router.go(-1);
        } else {
          alert('');
        }
      } catch(error) {
        console.error(':', error);
        if (error.response) {
          console.error(':', error.response);
        }
        alert('');
      }
    }
  },
  components: {
    Footer
  }
}
</script>
<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}
/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
/******************  ******************/
.wrapper .form-box {
  width: 100%;
  margin-top: 12vw;
}
.wrapper .form-box li {
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
  display: flex;
  align-items: center;
}
.wrapper .form-box li .title {
  flex: 0 0 18vw;
  font-size: 3vw;
  font-weight: 700;
  color: #666;
}
.wrapper .form-box li .content {
  flex: 1;
}
.wrapper .form-box li .content input {
  border: none;
  outline: none;
  width: 100%;
  height: 4vw;
  font-size: 3vw;
}
.wrapper .button-login {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
}
.wrapper .button-login button {
  width: 100%;
  height: 10vw;
  font-size: 3.8vw;
  font-weight: 700;
  color: #fff;
  background-color: #38CA73;
  border-radius: 4px;
  border: none;
  outline: none;
}
.wrapper .button-register {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
}
.wrapper .button-register button {
  width: 100%;
  height: 10vw;
  font-size: 3.8vw;
  font-weight: 700;
  color: #666;
  background-color: #EEE;
  border-radius: 4px;
  outline: none;
  border: solid 1px #DDD;
}
</style>
