<template>
  <div class="wrapper">

    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <ul class="business">
      <li v-for="item in businessArr" @click="toBusinessInfo(item.businessId)">
        <div class="business-img">
          <img :src="item.businessImg">
          <div class="business-img-quantity" v-show="item.quantity>0">
            {{item.quantity}}</div>
        </div>
        <div class="business-info">
          <h3>{{item.businessName}}</h3>
          <p>&#165;{{item.starPrice}} | &#165;{{item.deliveryPrice}}</p>
          <p>{{item.businessExplain}}</p>
        </div>
      </li>
    </ul>
    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from '@/views/Footer.vue';
import { businessApi } from '@/api/business';
import { cartApi } from '@/api/cart';

export default {
  name: 'BusinessList',
  data() {
    return {
      orderTypeId: this.$route.query.orderTypeId,
      businessArr: [],
      user: {}
    }
  },
  async created() {
    this.user = this.$getSessionStorage('user');
    if (!this.user) {
        this.$router.push('/login');
        return;
    }
    
    try {
        // orderTypeId
        const response = await businessApi.listBusinessByOrderTypeId(this.orderTypeId);
        if (response) {
            this.businessArr = response;
            // 
            await this.listCart();
        }
    } catch (error) {
        console.error(':', error);
        if (error === 'No token' || (error.response && error.response.status === 401)) {
            localStorage.removeItem('token');
            this.$removeSessionStorage('user');
            this.$router.push('/login');
        }
    }
  },
  components: {
    Footer
  },
  methods: {
    async listCart() {
      try {
        const response = await cartApi.listCart(this.user.userId);

        if (response) {
          // 
          for (let businessItem of this.businessArr) {
            this.$set(businessItem, 'quantity', 0);  //  Vue.set 
            for (let cartItem of response) {
              if (cartItem.businessId === businessItem.businessId) {
                this.$set(businessItem, 'quantity', businessItem.quantity + cartItem.quantity);
              }
            }
          }
        }
      } catch (error) {
        console.error(':', error);
        if (error === 'No token' || (error.response && error.response.status === 401)) {
          localStorage.removeItem('token');
          this.$removeSessionStorage('user');
          this.$router.push('/login');
        }
      }
    },
    toBusinessInfo(businessId) {
      this.$router.push({
        path: '/businessInfo',
        query: { businessId: businessId }
      });
    }
  }
}
</script>
<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}
/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
/******************  ******************/
.wrapper .business {
  width: 100%;
  margin-top: 12vw;
  margin-bottom: 14vw;
}
.wrapper .business li {
  width: 100%;
  box-sizing: border-box;
  padding: 2.5vw;
  border-bottom: solid 1px #DDD;
  user-select: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.wrapper .business li .business-img {
  /*business-img-quantity*/
  position: relative;
}
.wrapper .business li .business-img img {
  width: 20vw;
  height: 20vw;
}
.wrapper .business li .business-img .business-img-quantity {
  width: 5vw;
  height: 5vw;
  background-color: red;
  color: #fff;
  font-size: 3.6vw;
  border-radius: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  /**/
  position: absolute;
  right: -1.5vw;
  top: -1.5vw;
}
.wrapper .business li .business-info {
  margin-left: 3vw;
}
.wrapper .business li .business-info h3 {
  font-size: 3.8vw;
  color: #555;
}
.wrapper .business li .business-info p {
  font-size: 3vw;
  color: #888;
  margin-top: 2vw;
}
</style>
