<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <div class="order-info">
      <h5></h5>
      <div class="order-info-address" @click="toUserAddress">
        <div class="address-content">
          <p v-if="deliveryaddress && deliveryaddress.address" class="address-text">
            {{deliveryaddress.address}}
          </p>
          <p v-else class="no-address"></p>
          <p v-if="deliveryaddress && deliveryaddress.contactName" class="contact-info">
            {{deliveryaddress.contactName}}{{deliveryaddress.contactSex | sexFilter}} {{deliveryaddress.contactTel}}
          </p>
        </div>
        <i class="fa fa-angle-right"></i>
      </div>

    </div>
    <h3>{{business.businessName}}</h3>
    <!--  -->
    <div v-if="cartArr && cartArr.length > 0">
      <ul class="order-detailed">
        <li v-for="item in cartArr" :key="item.foodId">
          <div class="order-detailed-left">
            <img
              :src="item.food.foodImg"
              @error="handleImageError"
              :alt="item.food.foodName"
            >
            <div class="food-info">
              <p class="food-name">{{item.food.foodName}}</p>
              <p class="food-quantity">x {{item.quantity}}</p>
            </div>
          </div>
          <p class="food-price">&#165;{{(item.food.foodPrice * item.quantity).toFixed(2)}}</p>
        </li>
      </ul>
    </div>
    <div v-else class="empty-cart">
      <p></p>
    </div>
    <div class="order-deliveryfee">
      <p></p>
      <p>&#165;3</p>
    </div>
    <!--  -->
    <div class="total">
      <div class="total-left">
        &#165;{{totalPrice}}
      </div>
      <div class="total-right" @click="toPayment">
        
      </div>
    </div>
  </div>
</template>
<script>
import { businessApi } from '@/api/business'
import { cartApi } from '@/api/cart'
import { orderApi } from '@/api/order'
import { addressApi } from '@/api/address'

export default {
  name: 'Orders',
  data() {
    return {
      businessId: this.$route.query.businessId,
      business: {},
      user: {},
      cartArr: [],
      deliveryaddress: null
    }
  },
  async created() {
    this.user = this.$getSessionStorage('user')
    console.log(':', this.user)
    console.log('ID:', this.businessId)

    if (!this.user) {
      this.$router.push('/login')
      return
    }

    await this.loadAddress()
    await this.loadBusinessAndCart()
  },
  computed: {
    totalPrice() {
      try {
        const foodTotal = this.cartArr.reduce((sum, item) => {
          const price = parseFloat(item.food.foodPrice) || 0
          const quantity = parseInt(item.quantity) || 0
          return sum + (price * quantity)
        }, 0)

        const deliveryPrice = parseFloat(this.business.deliveryPrice) || 0
        const total = foodTotal + deliveryPrice

        console.log(':', {
          foodTotal,
          deliveryPrice,
          total
        })

        return total
      } catch (error) {
        console.error(':', error)
        return 0
      }
    }
  },
  filters: {
    sexFilter(value) {
      return value === 1 ? '' : ''
    }
  },
  methods: {
    // 
    async loadAddress() {
      try {
        // 
        const addressKey = `address_${this.user.userId}`
        const savedAddress = localStorage.getItem(addressKey)

        if (savedAddress) {
          this.deliveryaddress = JSON.parse(savedAddress)
          console.log(':', this.deliveryaddress)
          return
        }

        // 
        const addressResponse = await addressApi.listDeliveryAddressByUserId(this.user.userId)
        console.log(':', addressResponse)

        if (addressResponse && addressResponse.length > 0) {
          this.deliveryaddress = addressResponse[0]
          // 
          localStorage.setItem(addressKey, JSON.stringify(this.deliveryaddress))
          console.log(':', this.deliveryaddress)
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    async loadBusinessAndCart() {
      try {
        // 
        const businessResponse = await businessApi.getBusinessById(this.businessId)
        if (businessResponse) {
          this.business = businessResponse
          console.log(':', this.business)
        }

        // 
        await this.loadCartItems()
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    async loadCartItems() {
      try {
        const response = await cartApi.listCart(this.user.userId)
        console.log(':', response)

        if (response) {
          const cartArr = response
          // 
          const currentBusinessCart = []

          // 
          for (const cartItem of cartArr) {
            // 
            if (String(cartItem.businessId) === String(this.businessId)) {
              try {
                // 
                const foodResponse = await businessApi.getFoodById({
                  foodId: cartItem.foodId
                })
                if (foodResponse) {
                  currentBusinessCart.push({
                    ...cartItem,
                    food: foodResponse,
                    quantity: parseInt(cartItem.quantity) || 0
                  })
                }
              } catch (error) {
                console.error(':', error)
              }
            }
          }

          // 
          this.cartArr = currentBusinessCart.filter(item =>
            item.food && item.food.foodPrice && item.quantity > 0
          )
          console.log(':', this.cartArr)
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    toUserAddress() {
      this.$router.push({
        path: '/userAddress',
        query: {
          businessId: this.businessId,
          fromOrder: true
        }
      })
    },
    async toPayment() {
      if (!this.deliveryaddress) {
        this.$message.warning('')
        return
      }

      if (this.cartArr.length === 0) {
        this.$message.warning('')
        return
      }

      try {
        // 
        const orderData = {
          userId: this.user.userId,
          businessId: this.businessId,
          daId: this.deliveryaddress.daId,
          orderTotal: this.totalPrice,
          orderItems: this.cartArr.map(item => ({
            foodId: item.foodId,
            quantity: item.quantity,
            foodPrice: item.food.foodPrice
          }))
        }

        console.log(':', orderData)

        // 
        const orderId = await orderApi.createOrders(orderData)
        console.log('ID:', orderId)

        if (orderId > 0) {
          // ID
          this.$router.push({
            path: '/payment',
            query: {
              orderId: orderId
            }
          })
        } else {
          this.$message.error('')
        }
      } catch (error) {
        console.error(':', error.response?.data || error)
        if (error === 'No token' || (error.response && error.response.status === 401)) {
          localStorage.removeItem('token')
          this.$removeSessionStorage('user')
          this.$router.push('/login')
        } else {
          this.$message.error('')
        }
      }
    },
    handleImageError(e) {
      e.target.src = this.defaultImage
    }
  },
  activated() {
    if (this.user) {
      const addressKey = `address_${this.user.userId}`
      const savedAddress = localStorage.getItem(addressKey)
      if (savedAddress) {
        const newAddress = JSON.parse(savedAddress)
        // 
        if (!this.deliveryaddress || this.deliveryaddress.daId !== newAddress.daId) {
          this.deliveryaddress = newAddress
          console.log(':', this.deliveryaddress)
        }
      }
    }
  }
}
</script>
<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}
/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
/******************  ******************/
.wrapper .order-info {
  /**/
  width: 100%;
  margin-top: 12vw;
  background-color: #0097EF;
  box-sizing: border-box;
  padding: 2vw;
  color: #fff;
}
.wrapper .order-info h5 {
  font-size: 5vw;
  font-weight: 600;
  margin-bottom: 1vw;
  color: rgba(255, 255, 255, 0.95);
}
.wrapper .order-info .order-info-address {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vw 0;
}
.wrapper .order-info .address-content {
  flex: 1;
  margin-right: 3vw;
}
.wrapper .order-info .address-text {
  font-size: 4.5vw;
  font-weight: 500;
  margin-bottom: 1.5vw;
  word-break: break-all;
}
.wrapper .order-info .contact-info {
  font-size: 3.8vw;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 1.5vw;
}
.wrapper .order-info .no-address {
  font-size: 5.5vw;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 500;
  padding: 3vw 0;
  text-align: center;
}
.wrapper .order-info .fa-angle-right {
  font-size: 5vw;
  color: rgba(255, 255, 255, 0.8);
}
.wrapper .order-info p {
  font-size: 3vw;
}
.wrapper h3 {
  box-sizing: border-box;
  padding: 3vw;
  font-size: 4vw;
  color: #666;
  border-bottom: solid 1px #DDD;
}
/******************  ******************/
.wrapper .order-detailed {
  width: 100%;
}
.wrapper .order-detailed li {
  width: 100%;
  height: 16vw;
  box-sizing: border-box;
  padding: 3vw;
  color: #666;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.wrapper .order-detailed li .order-detailed-left {
  display: flex;
  align-items: center;
}
.wrapper .order-detailed li .order-detailed-left img {
  width: 10vw;
  height: 10vw;
}
.wrapper .order-detailed li .order-detailed-left .food-info {
  margin-left: 3vw;
}
.wrapper .order-detailed li .order-detailed-left .food-name {
  font-size: 3.8vw;
  color: #333;
  margin-bottom: 1vw;
}
.wrapper .order-detailed li .order-detailed-left .food-quantity {
  font-size: 3.2vw;
  color: #666;
}
.wrapper .order-detailed li .food-price {
  font-size: 3.8vw;
  color: #f60;
  font-weight: 500;
}
.wrapper .order-deliveryfee {
  width: 100%;
  height: 16vw;
  box-sizing: border-box;
  padding: 3vw;
  color: #666;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 3.5vw;
}
/******************  ******************/
.wrapper .total {
  width: 100%;
  height: 14vw;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
}
.wrapper .total .total-left {
  flex: 2;
  background-color: #505051;
  color: #fff;
  font-size: 4.5vw;
  font-weight: 700;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper .total .total-right {
  flex: 1;
  background-color: #38CA73;
  color: #fff;
  font-size: 4.5vw;
  font-weight: 700;
  user-select: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.empty-cart {
  width: 100%;
  padding: 40px 20px;
  text-align: center;
  color: #999;
  background: #f8f8f8;
  font-size: 4vw;
}
.user-info {
  font-size: 3.2vw;
  color: rgba(255, 255, 255, 0.9);
  margin-top: 1vw;
}
</style>
