import http from '@/utils/request'

export const userApi = {
    // 
    login(userId, password) {
        return http.post('UserController/getUserByIdByPass', {
            userId,
            password
        })
    },

    // 
    register(userInfo) {
        return http.post('UserController/saveUser', userInfo)
    },

    // ID
    checkUserId(userId) {
        return http.post('UserController/getUserById', {
            userId
        })
    },

    //  - GET
    getUserInfo(userId) {
        return http.get('UserController/getUserInfo', {
            userId
        })
    }
}

export default userApi
