<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <ul class="form-box">
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="text" v-model="userId" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="password" v-model="password" placeholder="">
        </div>
      </li>
    </ul>
    <div class="button-login">
      <button @click="login"></button>
    </div>
    <div class="button-register">
      <button @click="register"></button>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from '@/views/Footer.vue';
import { userApi } from '@/api/user';

export default{
  name:'Login',
  data(){
    return {
      userId:'',
      password:''
    }
  },
  methods:{
    async login(){
      if(this.userId === ''){
        alert('');
        return;
      }
      if(this.password === ''){
        alert('');
        return;
      }

      try {
        const response = await userApi.login(this.userId, this.password);
        console.log(':', response);
        
        if (response && response.token) {
          // token
          localStorage.setItem('token', response.token);
          // 
          this.$setSessionStorage('user', {
            userId: this.userId
          });
          
          // token
          setTimeout(() => {
            this.$router.push('/');
          }, 100);
        } else {
          alert('');
        }
      } catch(error) {
        console.error(':', error);
        if (error.response && error.response.status === 401) {
          alert('');
        } else {
          alert('');
        }
      }
    },
    register(){
      this.$router.push('/register');
    }
  },
  components:{
    Footer
  }
}
</script>
<style scoped>
/******************  ******************/
.wrapper {
  width: 100%;
  height: 100%;
}
/****************** header ******************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
/******************  ******************/
.wrapper .form-box {
  width: 100%;
  margin-top: 12vw;
}
.wrapper .form-box li {
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
  display: flex;
  align-items: center;
}
.wrapper .form-box li .title {
  flex: 0 0 18vw;
  font-size: 3vw;
  font-weight: 700;
  color: #666;
}
.wrapper .form-box li .content {
  flex: 1;
}
.wrapper .form-box li .content input {
  border: none;
  outline: none;
  width: 100%;
  height: 4vw;
  font-size: 3vw;
}
.wrapper .button-login {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
}
.wrapper .button-login button {
  width: 100%;
  height: 10vw;
  font-size: 3.8vw;
  font-weight: 700;
  color: #fff;
  background-color: #38CA73;
  border-radius: 4px;
  border: none;
  outline: none;
}
.wrapper .button-register {
  width: 100%;
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
}
.wrapper .button-register button {
  width: 100%;
  height: 10vw;
  font-size: 3.8vw;
  font-weight: 700;
  /**/
  color: #666;
  background-color: #EEE;
  border: solid 1px #DDD;
  border-radius: 4px;
  outline: none;
}
/******************  ******************/
.wrapper .footer {
  width: 100%;
  height: 14vw;
  border-top: solid 1px #DDD;
  background-color: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.wrapper .footer li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #999;
  user-select: none;
  cursor: pointer;
}
.wrapper .footer li p {
  font-size: 2.8vw;
}
.wrapper .footer li i {
  font-size: 5vw;
}
</style>
