<template>
  <div class="wrapper">
    <!-- header -->
    <header>
      <p></p>
    </header>
    <!--  -->
    <ul class="form-box">
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="text" v-model="deliveryAddress.contactName" placeholder="
">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content" style="font-size: 3vw;">
          <input type="radio" v-model="deliveryAddress.contactSex" value="1"
                 style="width:6vw;height:3.2vw;" checked>
          <input type="radio" v-model="deliveryAddress.contactSex" value="0"
                 style="width:6vw;height:3.2vw;">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="tel" v-model="deliveryAddress.contactTel" placeholder="">
        </div>
      </li>
      <li>
        <div class="title">
          
        </div>
        <div class="content">
          <input type="text" v-model="deliveryAddress.address" placeholder="">
        </div>
      </li>
    </ul>
    <div class="button-add">
      <button @click="editUserAddress"></button>
    </div>
    <!--  -->
    <Footer></Footer>
  </div>
</template>
<script>
import Footer from '@/views/Footer.vue'
import { addressApi } from '@/api/address'

export default {
  name: 'EditUserAddress',
  data() {
    return {
      businessId: this.$route.query.businessId,
      daId: this.$route.query.daId,
      user: {},
      deliveryAddress: {}
    }
  },
  async created() {
    this.user = this.$getSessionStorage('user')
    try {
      const response = await addressApi.getDeliveryAddressById(this.daId)
      this.deliveryAddress = response
      console.log(':', this.deliveryAddress)
    } catch (error) {
      console.error(':', error)
      this.$message.error('')
    }
  },
  components: {
    Footer
  },
  methods: {
    async editUserAddress() {
      // 
      if (!this.deliveryAddress.contactName?.trim()) {
        this.$message.warning('')
        return
      }
      if (!this.deliveryAddress.contactTel?.trim()) {
        this.$message.warning('')
        return
      }
      if (!this.validatePhone(this.deliveryAddress.contactTel)) {
        this.$message.warning('')
        return
      }
      if (!this.deliveryAddress.address?.trim()) {
        this.$message.warning('')
        return
      }

      try {
        // 
        const updateData = {
          daId: this.daId,
          userId: this.user.userId,
          contactName: this.deliveryAddress.contactName.trim(),
          contactSex: parseInt(this.deliveryAddress.contactSex),
          contactTel: this.deliveryAddress.contactTel.trim(),
          address: this.deliveryAddress.address.trim()
        }

        console.log(':', updateData)
        const response = await addressApi.updateDeliveryAddress(updateData)
        console.log(':', response)

        if (response > 0) {
          this.$message.success('')
          // 
          const addressKey = `address_${this.user.userId}`
          localStorage.setItem(addressKey, JSON.stringify(updateData))
          
          this.$router.push({
            path: '/userAddress',
            query: { businessId: this.businessId }
          })
        } else {
          this.$message.error('')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    validatePhone(phone) {
      const reg = /^1[3-9]\d{9}$/
      return reg.test(phone)
    }
  }
}
</script>
<style scoped>
/***************  ***************/
.wrapper {
  width: 100%;
  height: 100%;
}
/*************** header ***************/
.wrapper header {
  width: 100%;
  height: 12vw;
  background-color: #0097FF;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  font-size: 4.8vw;
  position: fixed;
  left: 0;
  top: 0;
  /**/
  z-index: 1000;
}
/***************  ***************/
.wrapper .form-box {
  width: 100%;
  margin-top: 12vw;
}
.wrapper .form-box li {
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
  display: flex;
}
.wrapper .form-box li .title {
  flex: 0 0 18vw;
  font-size: 3vw;
  font-weight: 700;
  color: #666;
}
.wrapper .form-box li .content {
  flex: 1;
  display: flex;
  align-items: center;
}
.wrapper .form-box li .content input {
  border: none;
  outline: none;
  width: 100%;
  height: 4vw;
  font-size: 3vw;
}
.wrapper .button-add {
  box-sizing: border-box;
  padding: 4vw 3vw 0 3vw;
}
.wrapper .button-add button {
  width: 100%;
  height: 10vw;
  font-size: 3.8vw;
  font-weight: 700;
  border: none;
  outline: none;
  border-radius: 4px;
  color: #fff;
  background-color: #38CA73;
}
</style>
