import http from '@/utils/request'

export const orderApi = {
    // 
    createOrders(orderData) {
        return http.post('OrdersController/createOrders', orderData)
    },

    // 
    listOrdersByUserId(params) {
        const data = new URLSearchParams()
        data.append('userId', params.userId)
        return http.post('/OrdersController/listOrdersByUserId', data)
    },

    // 
    getOrdersById(orderId) {
        const id = typeof orderId === 'object' ? orderId.orderId : orderId
        const data = new URLSearchParams()
        data.append('orderId', id)
        return http.post('/OrdersController/getOrdersById', data)
    },

    // 
    getOrderDetailetByOrderId(orderId) {
        return http.post('OrdersController/getOrderDetailetByOrderId', {
            orderId
        })
    },

    // 
    updateOrderState(params) {
        return http.post('OrdersController/updateOrderState', params)
    }
}

export default orderApi 